<script>
	import { ckmeans } from "simple-statistics";
	import {
		options,
		indicators,
		maps,
		colors,
		msoaBldg,
		msoaBounds,
	} from "./config";
	import { getData, getGeo, getCSV, getColor } from "./utils";
	import Chart from "./Chart.svelte";
	import Map from "./map/Map.svelte";
	import MapSource from "./map/MapSource.svelte";
	import MapLayer from "./map/MapLayer.svelte";

	const keys = Object.keys(options);

	let selected = {};
	let zKey = null;
	let zDomain = null;
	let rawdata = null;
	let data = null;
	let geo = null;
	let geodata = null;
	let breaks = null;
	let category = "All";
	let categoryPrev = category;
	let categoryHovered = null;
	let mapHovered = null;

	function doSelect() {
		let variables = [];
		let cat = [];
		keys.forEach((key) => {
			if (selected[key].code) {
				variables.push(options[key].code);
				cat.push(selected[key].name);
			}
		});

		getData(options, keys, variables).then((json) => {
			rawdata = json.data.dataset;
			processData();
			category = cat[0] ? cat.join(", ") : "All";
			if (selected[zKey] && !selected[zKey].code) {
				zKey = null;
			}

			if (category != "All") {
				getGeo(options, selected, keys, variables).then((json) => {
					const vals = json.data.dataset.table.values;
					let percs = [];
					let newgeo = JSON.parse(JSON.stringify(geo));

					vals.forEach((d, i) => {
						let perc = +((+d / +newgeo[i].pop) * 100).toFixed(2);
						newgeo[i].perc = perc;
						percs.push(perc);
					});

					let groups = ckmeans(percs, 5);
					let brks = [
						groups[0][0],
						groups[1][0],
						groups[2][0],
						groups[3][0],
						groups[4][0],
						groups[4][groups[4].length - 1],
					];

					newgeo.forEach((d) => {
						let quintile = getColor(d.perc, brks, colors.seq);
						d.color = quintile.color;
						d.group = quintile.group;
					});

					geodata = newgeo;
					breaks = brks;
					console.log(breaks, geodata);
				});
			} else {
				let newgeo = JSON.parse(JSON.stringify(geo));
				newgeo.forEach(d => {
					d.color = "rgba(0,0,0,0)";
					d.group = 5;
					d.perc = 100;
				});
				geodata = newgeo;
				breaks = null;
			}
		});
	}

	function processData() {
		let processedData = [];
		indicators.forEach((ind) => {
			let dataset = rawdata[ind.key];
			let dims = dataset.dimensions.length;
			let categories = dataset.dimensions[dims - 1].categories;
			let cats = categories.map((cat) => maps[cat.label]);

			let labels = ["All"];
			if (dims > 1) {
				for (let i = 0; i < dims - 1; i++) {
					let tempLabels = [];
					labels.forEach((label) => {
						dataset.dimensions[i].categories.forEach((d) => {
							let newLabel =
								label == "All" ? maps[d.label] : label + ", " + maps[d.label];
							tempLabels.push(newLabel);
						});
					});
					labels = tempLabels;
				}
			}

			let dataIndexed = {};
			let dataReversed = {};
			let dataArray = [];

			labels.forEach((label, i) => {
				dataIndexed[label] = { sum: 0, counts: [], perc: [] };

				categories.forEach((cat, j) => {
					let count =
						dataset.values[
							i * dataset.dimensions[dims - 1].categories.length + j
						];
					dataIndexed[label].sum += count;
					dataIndexed[label].counts.push({
						label: maps[cat.label],
						value: count,
					});
				});

				dataIndexed[label].counts.forEach((d) => {
					dataIndexed[label].perc.push({
						label: d.label,
						value: +((d.value / dataIndexed[label].sum) * 100).toFixed(2),
					});
				});

				cats.forEach((cat, i) => {
					let arr = label != "All" ? label.split(", ") : [];
					let obj = {};
					let j = 0;
					keys.forEach((key) => {
						if (selected[key].code) {
							obj[key] = arr[j];
							j += 1;
						}
					});
					if (!dataReversed[cat]) {
						dataReversed[cat] = { label: cat, data: [] };
					}
					dataReversed[cat].data.push({
						label: label,
						value: dataIndexed[label].perc[i].value,
						...obj,
					});
				});
			});

			cats.forEach((cat) => dataArray.push(dataReversed[cat]));

			processedData.push(dataArray);
		});

		data = processedData;
	}

	getData(options, keys).then((json) => {
		rawdata = json.data.dataset;
		processData();
	});

	getCSV("./data/msoa-code-name-pop.csv").then((res) => (geo = res));

	function updateDomain() {
		if (zKey) {
			let arr = [];
			data[0][0].data.forEach((d) => {
				if (!arr.includes(d[zKey])) {
					arr.push(d[zKey]);
				}
			});
			zDomain = arr;
		} else {
			zDomain = null;
		}
	}

	$: (zKey || !zKey) && updateDomain();

	$: if (category != categoryPrev) {
		let cats = category.split(", ");

		keys.forEach((key) => {
			if (selected[key].code && !cats.includes(selected[key].name)) {
				selected[key] = options[key].options.find((d) => cats.includes(d.name));
			}
		});
		categoryPrev = category;
		doSelect();
	}
</script>

<h1>How do I compare?</h1>

<p>
	Tell me how
	<!-- svelte-ignore a11y-no-onchange -->
	<select bind:value={selected.sex} on:change={doSelect}>
		{#each options.sex.options as option}
			<option value={option}>{option.label}</option>
		{/each}
	</select>
	aged
	<!-- svelte-ignore a11y-no-onchange -->
	<select bind:value={selected.age} on:change={doSelect}>
		{#each options.age.options as option}
			<option value={option}>{option.label}</option>
		{/each}
	</select>
	, born
	<!-- svelte-ignore a11y-no-onchange -->
	<select bind:value={selected.birth} on:change={doSelect}>
		{#each options.birth.options as option}
			<option value={option}>{option.label}</option>
		{/each}
	</select>
	and of
	<!-- svelte-ignore a11y-no-onchange -->
	<select bind:value={selected.ethnicity} on:change={doSelect}>
		{#each options.ethnicity.options as option}
			<option value={option}>{option.label}</option>
		{/each}
	</select>
	ethnicity compare to other sub-population groups in England and Wales.
</p>

<div class="no-whitespace">
	Colour by:
	<label>
		<input type="radio" bind:group={zKey} value={null} />
		None
	</label>
	{#each keys as key}
		{#if selected[key] && selected[key].code}
			<label>
				<input type="radio" bind:group={zKey} value={key} />
				{options[key].label}
			</label>
		{/if}
	{/each}

	{#if Array.isArray(zDomain) && zDomain[0]}
		<span class="title">Legend:</span>
		{#each zDomain as val, i}
			<span style="color: {colors.cat[i]}">{val}</span>
		{/each}
	{/if}
</div>

{#if data}
	{#each indicators as ind, i}
		<h2>{ind.label}</h2>
		<div class="grid">
			{#each data[i] as cat, j}
				{#if j == 0}
					<div />
					<div>
						<Chart data={[]} markers={false} axis={true} height={12} />
					</div>
				{/if}
				<div>{cat.label}</div>
				<div>
					<Chart
						data={cat.data}
						xKey="value"
						{zKey}
						{zDomain}
						colors={colors.cat}
						axis={true}
						axisLabels={false}
						bind:selected={category}
						bind:hovered={categoryHovered}
					/>
				</div>
			{/each}
		</div>
	{/each}
{/if}

{#if category == "All"}
	<h2>Select a group to see the map</h2>
{:else}
	<h2>% of people who are {category.toLowerCase().replace("uk", "UK")}</h2>
{/if}

<div class="grid">
	<div />
	<div>
		<Chart
			data={[]}
			xDomain={breaks ? [breaks[0], breaks[5]] : [0, 100]}
			markers={false}
			axis={true}
			height={12}
			ticks={breaks ? breaks : 4}
		/>
	</div>
	<div>By neighbourhood</div>
	<div>
		<Chart
			data={!geodata || geodata[0].color == "rgba(0,0,0,0)" ? [] : geodata}
			xKey="perc"
			yKey="code"
			zKey="group"
			xDomain={breaks ? [breaks[0], breaks[5]] : [0, 100]}
			zDomain={[1, 2, 3, 4, 5]}
			colors={colors.seq}
			axis={true}
			axisLabels={false}
			ticks={breaks ? breaks : 4}
			bind:hovered={mapHovered}
		/>
	</div>
</div>

<div style="height: 10px;" />

<Map location={{ lon: -1.8904, lat: 52.4862, zoom: 10 }}>
	{#if geodata}
		<MapSource
			id="msoa-buildings"
			type="vector"
			url={msoaBldg.url}
			layer={msoaBldg.layer}
			promoteId={msoaBldg.code}
			maxzoom={13}
		>
			<MapLayer
				id="msoa-buildings"
				source="msoa-buildings"
				sourceLayer={msoaBldg.layer}
				data={geodata}
				geoCode="code"
				colorCode="color"
				type="fill"
				paint={{
					"fill-color": [
						"case",
						["!=", ["feature-state", "color"], null],
						["feature-state", "color"],
						"rgba(255, 255, 255, 0)",
					],
				}}
				order="aeroway-taxiway"
			/>
		</MapSource>
		<MapSource
			id="msoa-bounds"
			type="vector"
			url={msoaBounds.url}
			layer={msoaBounds.layer}
			promoteId={msoaBounds.code}
			maxzoom={13}
		>
			<MapLayer
				id="msoa-fill"
				source="msoa-bounds"
				sourceLayer={msoaBounds.layer}
				data={geodata}
				geoCode="code"
				nameCode="name"
				valueCode="perc"
				hover={true}
				bind:hovered={mapHovered}
				tooltip={true}
				type="fill"
				paint={{
					"fill-color": "rgba(255, 255, 255, 0)",
				}}
				order="highway_name_other"
			/>
			<MapLayer
				id="msoa-line"
				source="msoa-bounds"
				sourceLayer={msoaBounds.layer}
				type="line"
				paint={{
					"line-color": "orange",
					"line-width": 2,
					"line-opacity": [
						"case",
						["==", ["feature-state", "hovered"], true],
						1,
						0,
					],
				}}
				order="highway_name_other"
			/>
		</MapSource>
	{/if}
</Map>

<div style="height: 35px;" />

<style>
	:global(body) {
		padding: 0 25px 0 25px;
		background-color: #111;
		color: #ddd;
	}
	h1 {
		color: #eee;
	}
	h2 {
		margin: 30px 0 10px 0;
		color: #eee;
	}
	.grid {
		display: grid;
		width: 100%;
		grid-gap: 0;
		grid-template-columns: 150px auto;
		margin: 24px 0 0 0;
	}
	.no-whitespace {
		display: flex;
		align-items: baseline;
	}
	.no-whitespace label,
	.no-whitespace span {
		margin-left: 10px;
	}
	.title {
		margin-left: 30px !important;
	}
</style>
